import {ChangeDetectionStrategy, Component, Input, OnInit} from "@angular/core";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {WitnessHostData} from "@generated/witness-host-data";
import {FaultDomainModel} from "@model/fault-domain.model";
import {FaultDomainsUtil} from "@util/fault-domains-util";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {FaultDomainConsumptionData} from "@model/fault-domain-consumption-data";

@Component({
   selector: "fault-domains-summary",
   templateUrl: "fault-domains-summary.html",
   styleUrls: ["fault-domains-summary.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FaultDomainsSummaryComponent implements OnInit {
   @Input()
   isVsanMaxEnabled: boolean;

   @Input()
   availableDomains: DomainOrHostData[];

   @Input()
   storageConsumption: any;

   @Input()
   witnessInfos: WitnessHostData[];

   @Input()
   clusterCapabilityData: VsanCapabilityData;

   @Input()
   isStretchedCluster: boolean;

   @Input()
   isSharedWitnessAllowed: boolean;

   faultToleranceInfo: string;
   faultDomainModels: FaultDomainModel[] = [];
   standaloneModels: FaultDomainModel[] = [];
   private preferredDomain: string;

   ngOnInit(): void {
      this.preferredDomain = FaultDomainsUtil.getPreferredDomain(this.witnessInfos);
      this.standaloneModels = FaultDomainsUtil.getStandaloneModels(this.availableDomains, this.witnessInfos);
      this.faultDomainModels = FaultDomainsUtil.getFaultDomainModels(this.availableDomains, this.preferredDomain);
      this.faultToleranceInfo = FaultDomainsUtil.getFaultToleranceInfo(
            this.availableDomains,
            this.witnessInfos,
            this.standaloneModels,
            this.faultDomainModels,
            this.storageConsumption);
   }

   getDomainConsumption(model: FaultDomainModel): FaultDomainConsumptionData {
      return FaultDomainConsumptionData.getDomainConsumptionData(model, this.storageConsumption);
   }
}
