import {NgModule} from "@angular/core";
import {RouterModule, Routes} from "@angular/router";
import {VsanClusterOverviewComponent} from "./vsan-cluster-overview.component";
import {VsanCommonModule} from "../common/vsan-common.module";
import {VsanChartModule} from "@component/chart/chart.module";
import {DragAndDropModule} from "@component/drag-and-drop/drag-and-drop.module";
import {CardModule} from "@component/card/card.module";
import {ClarityModule} from "@clr/angular";
import {UpgradeModule} from "@component/upgrade/upgrade.module";
import {CommonModule} from "@angular/common";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {CapacityCommonModule} from "@component/capacity/capacity-common.module";
import {VsanCapacityService} from "@component/capacity/capacity.service";
import {FormsModule} from "@angular/forms";
import {CapacityCardComponent} from "./capacity-card.component";
import {HealthCardComponent} from "./health-card.component";
import {HealthCheckItemModule} from "@component/health/health-check-item.module";
import {DetailViewWrapperComponent} from "./detail-view-wrapper.component";
import {PersistentStorageService} from "@service/browser-storage.service";
import {ConfigurationsCardComponent} from "./configurations-card.component";
import {VsanConfigService} from "@generated/vsan-config-service";
import {ConfigurationsModule} from "@component/configurations/configurations.module";
import {HistoricalHealthComponent} from "./historical-health.component";
import {CapacityManagementService} from "@generated/capacity-management-service";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {CapacityDataService} from "@generated/capacity-data-service";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {FaultDomainsSummaryComponent} from "./fault-domains-summary.component";
import {ResyncingDataCardComponent} from "./resyncing-data-card.component";
import {VsanResyncingComponentsProvider} from "@generated/vsan-resyncing-components-provider";

const routes: Routes = [
   {
      path: "",
      component: VsanClusterOverviewComponent,
   },
   {
      path: "details",
      component: DetailViewWrapperComponent,
      children: [
         {
            path: "capacity",
            loadChildren: () => import("../capacity/capacity.module")
                  .then(mod => mod.CapacityModule),
            outlet: "details",
         },
         {
            path: "health",
            loadChildren: () => import("../health/health.module")
                  .then(mod => mod.HealthModule),
            outlet: "details",
         },
         {
            path: "services",
            loadChildren: () => import("../services/services.module")
                  .then(mod => mod.ServicesModule),
            outlet: "details",
         },
         {
            path: "faultDomains",
            loadChildren: () => import("../faultdomains/fault-domains.module")
                  .then(mod => mod.FaultDomainsModule),
            outlet: "details",
         },
         {
            path: "resyncingComponents",
            loadChildren: () => import("../resync/resync-monitor.module")
                  .then(mod => mod.ResyncMonitorModule),
            outlet: "details",
         },
      ],
   },
];

@NgModule({
   imports: [
      CommonModule,
      FormsModule,
      VsanCommonModule,
      VsanChartModule,
      CapacityCommonModule,
      ClarityModule,
      DragAndDropModule,
      CardModule,
      UpgradeModule,
      HealthCheckItemModule,
      RouterModule.forChild(routes),
      ConfigurationsModule,
   ],
   declarations: [
      VsanClusterOverviewComponent,
      CapacityCardComponent,
      HealthCardComponent,
      DetailViewWrapperComponent,
      ConfigurationsCardComponent,
      HistoricalHealthComponent,
      FaultDomainsSummaryComponent,
      ResyncingDataCardComponent,
   ],
   providers: [
      VsanConfigService,
      VsanGeneralConfigService,
      VsanCapacityService,
      PersistentStorageService,
      CapacityManagementService,
      VsanCapabilityProvider,
      CapacityDataService,
      VsanStretchedClusterService,
      VsanResyncingComponentsProvider,
   ],
})
export class VsanClusterOverviewModule {}
