import {Component, OnInit, QueryList, ViewChildren} from "@angular/core";
import {CardName, CardView} from "@component/drag-and-drop/card-view";
import {CardContainer, DragCardPosition} from "@component/drag-and-drop/drag-card-position";
import {DraggableCardDirective} from "@component/drag-and-drop/draggable-card.directive";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {UpgradeData} from "@component/upgrade/upgrade.data";
import {UpgradeService} from "@component/upgrade/upgrade.service";
import {environment} from "@env/environment";
import {CeipService} from "@generated/ceip-service";
import {ClusterMode} from "@generated/cluster-mode";
import {Privileges} from "@generated/privileges";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ClusterComputeResource, VirtualMachine} from "@service/client/dataservice/data-service-property";
import {DataServiceUtil} from "@service/client/dataservice/data-service-util";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {TaskMonitorService} from "@service/task-monitor-service";
import {VsanUiUtils} from "@util/vsan-util";
import {QuickstartNotificationService} from "../hci/status/quickstart-notification.service";
import {DetailsViewContext} from "./details-view-context";

@Component({
   selector: "vsan-cluster-overview",
   templateUrl: "vsan-cluster-overview.html",
   styleUrls: ["vsan-cluster-overview.scss"],
})
export class VsanClusterOverviewComponent implements OnInit {
   // cards
   readonly CAPACITY_CARD: string = "capacityService";
   readonly HEALTH_CARD: string = "healthSummaryService";
   readonly CONFIGURATION_CARD: string = "configurationService";
   readonly RESYNCING_OBJECTS_CARD: string = "resyncingObjectService";

   clusterRef: ManagedObject;
   cardsView: CardView;

   // Common config data
   vsanConfig: VsanConfigSpec;
   isComputeOnlyCluster: boolean;
   upgradeData: UpgradeData;
   selectedHealthTestId: string;

   // Permissions
   clusterPermissions: Map<string, boolean>;
   vcPermissions: Map<string, boolean>;
   editClusterPermission: boolean = false;
   configureStoragePermission: boolean = false;

   // Capabilities
   vcCapabilityData: VsanCapabilityData;
   clusterCapabilityData: VsanCapabilityData;

   isVsanUpgradeVisible: boolean = false;
   showCards: boolean = true;
   detailViewContext: DetailsViewContext;

   errorMessages: string[] = [];

   // The cards are defined inside templates, so they can be dragged
   @ViewChildren(DraggableCardDirective) serviceTemplates: QueryList<DraggableCardDirective>;

   @LoadingIndicator()
   isLoading: boolean;

   constructor(private dataService: DataServiceClient,
         private vsanGeneralConfigService: VsanGeneralConfigService,
         private ceipSvc: CeipService,
         private taskService: TaskMonitorService,
         private quickstartStatusNotification: QuickstartNotificationService,
         private upgradeService: UpgradeService) {
      this.cardsView = new CardView(CardName.VSAN_HOME_CARD_VIEW, this.getViewCards);
   }

   async ngOnInit(): Promise<void> {
      // TODO: [rogerw] Right now we can only have the vSAN tab at VM level. If we get approval to add this tab
      // to cluster level, this.getVmCluster() method call should be removed.
      this.clusterRef = await this.getVmCluster();
      if (this.showCards) {
         await this.refresh();
      }
   }

   private async getVmCluster(): Promise<ManagedObject> {
      if (environment.repa) {
         const clusterId: string = await this.dataService.getProperty(VirtualMachine.cluster, ManagedObject.contextObject);
         return DataServiceUtil.moIdToManagedObject(ClusterComputeResource.type, clusterId);
      } else {
         return await this.dataService.getProperty(VirtualMachine.cluster, ManagedObject.contextObject);
      }
   }

   @RefreshHandler
   @Loader()
   async refresh(): Promise<void> {
      this.reset();
      await this.queryVsanGeneralConfig();
      if (!this.vsanConfig) {
         this.errorMessages = [VsanUiUtils.getString("vsan.services.configuration.not.available")];
         return;
      }

      if (!this.isComputeOnlyCluster) {
         this.fetchDisksUpgradeData();
      }
   }

   @ErrorHandler()
   handleError(error: string): void {
      this.errorMessages = [error];
   }

   fetchPrecheckUpgradeData(): void {
      this.upgradeService.getUpgradeStatus(this.clusterRef)
            .then(upgradeStatus => {
               // Create a new instance to trigger the upgrade component's refresh
               this.upgradeData = this.upgradeData.getNewInstance(upgradeStatus);
            })
            .catch(e => this.errorMessages.push(e));
   }

   vsanUpgradeComplete(): void {
      this.isVsanUpgradeVisible = true;
      this.fetchDisksUpgradeData();
   }

   fetchDisksUpgradeData(): void {
      this.upgradeData = null;
      this.upgradeService.getUpgradeData(this.clusterRef)
            .then(data => this.upgradeData = data)
            .catch(e => this.errorMessages.push(e));
   }

   async queryVsanGeneralConfig(): Promise<void> {
      try {
         const generalConfig: GeneralConfigData = await this.vsanGeneralConfigService.getVsanGeneralConfigData(
               this.clusterRef);
         this.vsanConfig = generalConfig.cloneVsanConfigSpec();
         this.vcCapabilityData = generalConfig.vcCapabilityData;
         this.clusterCapabilityData = generalConfig.clusterCapabilityData;
         this.clusterPermissions = generalConfig.clusterPermissions;
         this.vcPermissions = generalConfig.vcPermissions;
         this.editClusterPermission = this.clusterPermissions.get(Privileges.EDIT_CLUSTER());
         this.configureStoragePermission = this.clusterPermissions.get(Privileges.CONFIG_STORAGE());
         this.isComputeOnlyCluster = this.vsanConfig && this.vsanConfig.clusterMode === ClusterMode.COMPUTE;
      } catch (e) {
         this.errorMessages.push(e);
      }
   }

   showDetailsView(context: DetailsViewContext): void {
      this.detailViewContext = context;
      this.showCards = false;
   }

   private reset(): void {
      this.errorMessages = [];
      this.upgradeData = null;
   }

   private getViewCards = (): Map<string, DragCardPosition> => {
      const result: Map<string, DragCardPosition> = new Map<string, DragCardPosition>();
      result.set(this.CAPACITY_CARD, new DragCardPosition(CardContainer.LEFT, 0));
      result.set(this.CONFIGURATION_CARD, new DragCardPosition(CardContainer.LEFT, 1));
      result.set(this.HEALTH_CARD, new DragCardPosition(CardContainer.RIGHT, 0));
      result.set(this.RESYNCING_OBJECTS_CARD, new DragCardPosition(CardContainer.RIGHT, 1));
      return result;
   };
}
