import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanResyncingComponentsProvider} from "@generated/vsan-resyncing-components-provider";
import {RefreshHandler} from "@service/global-refresh.service";
import {Loader} from "@service/flow/loader.decorator";
import {ResyncMonitorData} from "@generated/resync-monitor-data";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {VsanUiUtils} from "@util/vsan-util";
import {ResyncObjectsUtil} from "../resync/resync-objects-util";
import {DetailsViewContext} from "./details-view-context";
import {VsanOverviewDetailsView} from "./vsan-overview-details-view";

@Component({
   selector: "resyncing-data-card",
   templateUrl: "resyncing-data-card.html",
   styleUrls: ["resyncing-data-card.scss"],
})
export class ResyncingDataCardComponent implements OnInit {
   @Input()
   clusterRef: ManagedObject;

   @Input()
   isResyncETAImprovementSupported: boolean;

   @Output()
   onShowDetails: EventEmitter<DetailsViewContext> = new EventEmitter<DetailsViewContext>();

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   error: string;

   resyncData: ResyncMonitorData;
   resyncingComponents: string;
   bytesLeft: string;
   totalResyncingEta: number;
   scheduledResyncing: string;
   suspendedResync: boolean;

   constructor(private vsanResyncProvider: VsanResyncingComponentsProvider) {}

   async ngOnInit(): Promise<void> {
      this.refresh();
   }

   showDetail(): void {
      const context: DetailsViewContext = {
         view: VsanOverviewDetailsView.RESYNCINGOBJECTS,
         content: this.clusterRef,
      };
      this.onShowDetails.emit(context);
   }

   @RefreshHandler
   private async refresh(): Promise<void> {
      await this.fetch();
      this.resyncingComponents = VsanUiUtils.isUnset(this.resyncData.componentsToSync)
            ? VsanUiUtils.getString("vsan.na.label")
            : this.resyncData.componentsToSync.toString();
      this.bytesLeft = VsanUiUtils.isUnset(this.resyncData.bytesToResync)
            ? VsanUiUtils.getString("vsan.na.label")
            : VsanUiUtils.formatFileSize(this.resyncData.bytesToResync);
      // show suspended resync status label if there are only suspended bytes to resync and the returned ETA is 0
      this.suspendedResync = !!this.resyncData.suspendedBytesToResync
            && !this.resyncData.suspendedETA
            && !this.resyncData.activeBytesToResync
            && !this.resyncData.queuedBytesToResync;
      this.totalResyncingEta = this.calculateTotalResyncingEta();
      this.scheduledResyncing = ResyncObjectsUtil.getScheduledResync(this.resyncData,
            this.isResyncETAImprovementSupported);
   }

   private calculateTotalResyncingEta(): number {
      const hasTotalResyncObjects: boolean = !!this.resyncData?.bytesToResync
            && (!!this.resyncData?.activeBytesToResync || !!this.resyncData?.queuedBytesToResync);
      return hasTotalResyncObjects ? 0 : this.resyncData.etaToResync;
   }

   @Loader()
   private async fetch(): Promise<void> {
      if (this.isResyncETAImprovementSupported) {
         this.resyncData = await this.vsanResyncProvider.getResyncingDataForAutoRefresh(this.clusterRef);
      } else {
         this.resyncData = await this.vsanResyncProvider.getResyncingData(this.clusterRef, 100);
      }
   }
}
