import {Component, Input, OnInit} from "@angular/core";
import {Router} from "@angular/router";
import {ManagedObject} from "@service/managed-object";
import {DetailsViewContext} from "./details-view-context";
import {VsanOverviewDetailsView} from "./vsan-overview-details-view";
import {HealthContext} from "@navigation/context/health-context";
import {PersistentStorageService} from "@service/browser-storage.service";
import {VsanRoutes} from "../../vsan-routes";
import {InlineViewContextKey} from "@model/inline-view-context-key.data";

@Component({
   selector: "detail-view-wrapper",
   templateUrl: "detail-view-wrapper.html",
   styleUrls: ["detail-view-wrapper.scss"],
})
export class DetailViewWrapperComponent implements OnInit {
   private readonly OVERVIEW_DETAILS_PATH: string = "details";
   @Input()
   clusterRef: ManagedObject;

   @Input()
   context: DetailsViewContext;

   constructor(private router: Router, private storageService: PersistentStorageService) {}

   ngOnInit(): void {
      this.expandDetailsView();
   }

   backToOverview(): void {
      this.router.navigate(["/" + VsanRoutes.CLUSTER_OVERVIEW_VIEW]);
   }

   private expandDetailsView(): void {
      switch (this.context?.view) {
         case VsanOverviewDetailsView.HEALTH:
            this.setHealthContext();
            this.navigateToDetails(VsanRoutes.HEALTH_VIEW);
            break;
         case VsanOverviewDetailsView.CAPACITY:
            this.navigateToDetails(VsanRoutes.CAPACITY_VIEW);
            break;
         case VsanOverviewDetailsView.SERVICES:
            this.setGeneralContext(InlineViewContextKey.SERVICES_CONTEXT_KEY);
            this.navigateToDetails(VsanRoutes.SERVICES_VIEW);
            break;
         case VsanOverviewDetailsView.FAULTDOMAINS:
            this.setGeneralContext(InlineViewContextKey.FAULT_DOMAINS_CONTEXT_KEY);
            this.navigateToDetails(VsanRoutes.FAULT_DOMAINS_VIEW);
            break;
         case VsanOverviewDetailsView.RESYNCINGOBJECTS:
            this.setGeneralContext(InlineViewContextKey.RESYNCING_OBJECTS_CONTEXT_KEY);
            this.navigateToDetails(VsanRoutes.RESYNCING_COMPONENTS_VIEW);
            break;
      }
   }

   private setHealthContext(): void {
      this.storageService.set(InlineViewContextKey.HEALTH_CHECK_CONTEXT_KEY, this.healthContext);
   }

   private navigateToDetails(details: string): void {
      this.router.navigate([
            "/" + VsanRoutes.CLUSTER_OVERVIEW_VIEW + "/" + this.OVERVIEW_DETAILS_PATH,
            {outlets: {"details": [details]}},
         ]);
   }

   private get healthContext(): HealthContext {
      const healthContext: HealthContext = this.context.content as HealthContext;
      healthContext.clusterRef = this.clusterRef;
      return healthContext;
   }

   private setGeneralContext(key: string): void {
      this.storageService.set(key, this.clusterRef);
   }
}
