import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from '@service/flow/loader.decorator';
import {RefreshHandler} from '@service/global-refresh.service';
import {VsanHealthData} from "@generated/vsan-health-data";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {VsanHealthCheck} from "@generated/vsan-health-check";
import {VsanTestData} from "@generated/vsan-test-data";
import {HealthContext} from "@navigation/context/health-context";
import {DetailsViewContext} from "./details-view-context";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanOverviewDetailsView} from "./vsan-overview-details-view";

@Component({
   selector: "health-card",
   templateUrl: "health-card.html",
   styleUrls: ["health-card.scss"],
})
export class HealthCardComponent implements OnInit {
   @Input()
   clusterRef: ManagedObject;

   @Output()
   onShowDetails: EventEmitter<DetailsViewContext> = new EventEmitter<DetailsViewContext>();

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   error: string;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   healthTestData: VsanHealthCheck[];

   constructor(private healthProvider: VsanHealthPropertyProvider) {}

   async ngOnInit(): Promise<void> {
      await this.refresh();
      this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
            VsanUiUtils.getString("vsan.monitor.capacity.overview.title"));
   }

   @RefreshHandler
   @Loader()
   async refresh(): Promise<void> {
      const healthData: VsanHealthData = await this.healthProvider.getVsanHealthSummaryFromCache(this.clusterRef);
      this.healthTestData = [];
      healthData.testsData
            .filter(test => this.isUnhealthyItem(test))
            .filter(test => !VsanUiUtils.isEmpty(test.subtests))
            .forEach(test => {
               const subtests: VsanHealthCheck[] = test.subtests
                     .filter(subtest => this.isUnhealthyItem(subtest))
                     .map(subtest => this.createVsahHealthCheck(test, subtest));
               this.healthTestData.push(...subtests);
            });
   }

   showHealthView(healthContext: HealthContext): void {
      const context: DetailsViewContext = {
         view: VsanOverviewDetailsView.HEALTH,
         content: healthContext,
      };

      this.onShowDetails.emit(context);
   }

   private createVsahHealthCheck(test: VsanTestData, subtest: VsanTestData): VsanHealthCheck {
      return VsanHealthCheck.Factory.create(
            "defaultView",
            test.testName,
            subtest.testId,
            subtest.testName,
            subtest.testName,
            subtest.status,
            false);
   }

   private isUnhealthyItem(item: VsanTestData): boolean {
      return item.status === VsanHealthStatus.red || item.status === VsanHealthStatus.yellow;
   }

   @ErrorHandler()
   handleError(error: string): void {
      this.error = error;
   }
}
