import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {DetailsViewContext} from "./details-view-context";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {VsanConfigService} from "@generated/vsan-config-service";
import {VsanServiceData} from "@generated/vsan-service-data";
import {PerfStatsObjectInfo} from "@generated/perf-stats-object-info";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {Loader} from "@service/flow/loader.decorator";
import {VsanOverviewDetailsView} from "./vsan-overview-details-view";
import {VsanIscsiTargetConfig} from "@generated/vsan-iscsi-target-config";
import {Privileges} from "@generated/privileges";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {CapacityManagementService} from "@generated/capacity-management-service";
import {CapacityManagementData} from "@generated/capacity-management-data";
import {WitnessHostData} from "@generated/witness-host-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {CapacityDataService} from "@generated/capacity-data-service";
import {HostStorageConsumptionData} from "@generated/host-storage-consumption-data";
import {ErrorHandler} from "@service/flow/error-handler.decorator";

@Component({
   selector: "configurations-card",
   templateUrl: "configurations-card.html",
   styleUrls: ["configurations-card.scss"],
})
export class ConfigurationsCardComponent implements OnInit {
   @Input()
   clusterRef: ManagedObject;

   @Input()
   vcCapabilityData: VsanCapabilityData;

   @Input()
   clusterCapabilityData: VsanCapabilityData;

   @Input()
   vcPermissions: Map<string, boolean>;

   @Input()
   clusterPermissions: Map<string, boolean>;

   @Output()
   onShowDetails: EventEmitter<DetailsViewContext> = new EventEmitter<DetailsViewContext>();

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   error: string;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   @LoadingIndicator({name: "fault-domains"})
   loadingFaultDomains: boolean;

   vsanConfig: VsanConfigSpec;

   availableDomains: DomainOrHostData[];
   isStretchedCluster: boolean;
   witnessInfos: WitnessHostData[] = null; // will be null if stretched cluster/metadata only node is disabled
   isSharedWitnessAllowed: boolean;
   storageConsumption: any;
   faultDomainTabActive: boolean;

   hasEditClusterPermission: boolean;

   perfSvcStatus: VsanServiceStatus;
   perfSvcConfig: PerfStatsObjectInfo;
   perfSvcEnabled: boolean;

   iscsiSvcStatus: VsanServiceStatus;
   iscsiSvcConfig: VsanIscsiTargetConfig;

   capacityManagementData: CapacityManagementData;

   errorMessages: string[] = [];

   constructor(private configService: VsanConfigService,
         private vsanGeneralConfigService: VsanGeneralConfigService,
         private capacityManagementService: CapacityManagementService,
         private capabilityProvider: VsanCapabilityProvider,
         private capacityDataService: CapacityDataService,
         private stretchedClusterService: VsanStretchedClusterService) {}

   ngOnInit(): void {
      this.srOnlyMessage = "";
      this.hasEditClusterPermission = this.clusterPermissions.get(Privileges.EDIT_CLUSTER());
      this.fetch();
   }

   @RefreshHandler
   @Loader()
   async fetch(): Promise<void> {
      this.reset();
      await Promise.all([
            this.queryVsanGeneralConfig(),
            this.queryPerfServiceConfig(),
            this.fetchStretchedClusterData(),
            this.fetchIsStretchedCluster(),
      ]);
   }

   private async queryVsanGeneralConfig(): Promise<void> {
      const generalConfig: GeneralConfigData = await this.vsanGeneralConfigService.getVsanGeneralConfigData(
            this.clusterRef);
      this.vsanConfig = generalConfig.cloneVsanConfigSpec();
   }

   private async queryPerfServiceConfig(): Promise<void> {
      const result: VsanServiceData = await this.configService.getPerformanceStatus(this.clusterRef);
      this.perfSvcStatus = result.status;
      this.perfSvcConfig = result.details as PerfStatsObjectInfo;
      this.perfSvcEnabled = result.status === VsanServiceStatus.ENABLED;
   }

   private async fetchStretchedClusterData(): Promise<any> {
      this.availableDomains = await this.stretchedClusterService.getAvailableDomains(this.clusterRef);
   }

   private async fetchIsStretchedCluster(): Promise<any> {
      this.isStretchedCluster = await this.stretchedClusterService.getIsVsanStretchedCluster(this.clusterRef);
   }

   async queryIscsiTargetConfig(): Promise<void> {
      if (this.iscsiSvcConfig) {
         return;
      }
      const result: VsanServiceData = await this.configService.getIscsiTargetStatus(this.clusterRef);
      this.iscsiSvcConfig = result.details as VsanIscsiTargetConfig;
      this.iscsiSvcStatus = result.status;
   }

   async queryCapacityMgmtSettings(): Promise<void> {
      this.capacityManagementData = await this.capacityManagementService.getCapacityManagementData(this.clusterRef);
   }

   @Loader({name: "fault-domains"})
   async queryStretchedClusterSettings(): Promise<void> {
      const hostsStorageData: HostStorageConsumptionData[] =
            await this.capacityDataService.getStorageConsumptionByHost(this.clusterRef);
      this.storageConsumption = this.processHostsCapacity(hostsStorageData);
      this.witnessInfos = await this.stretchedClusterService.getWitnessHostData(this.clusterRef);
      if (this.vcCapabilityData.isSharedWitnessSupported) {
         this.isSharedWitnessAllowed = await this.checkIfSharedWitnessIsAllowed();
      }
   }

   @ErrorHandler()
   private handleError(error: string): void {
      this.errorMessages.push(error);
   }

   private async checkIfSharedWitnessIsAllowed(): Promise<boolean> {
      if (!this.isStretchedCluster) {
         return false;
      }
      const allHostsCount: number =
            this.availableDomains.reduce((sum, item) => sum += item.isHost ? 1 : item.children.length, 0);
      if (allHostsCount === 2) {
         const hostRef: ManagedObject = this.witnessInfos[0].witnessHost;
         return await this.capabilityProvider.getIsSharedWitnessSupported(hostRef);
      }
      return false;
   }

   private processHostsCapacity(hostsStorageData: HostStorageConsumptionData[]): any {
      const storageConsumption: any = {};
      hostsStorageData.forEach(item => {
         if (item.hostRef) {
            storageConsumption[ManagedObject.uid(item.hostRef)] = item;
         }
      });
      return storageConsumption;
   }

   private reset(): void {
      this.errorMessages = [];
      this.perfSvcStatus = null;
      this.perfSvcConfig = null;
      this.iscsiSvcStatus = null;
      this.iscsiSvcConfig = null;
      this.availableDomains = [];
      this.capacityManagementData = null;
      this.isStretchedCluster = false;
   }

   showDetail(): void {
      const context: DetailsViewContext = {
         view: this.faultDomainTabActive ? VsanOverviewDetailsView.FAULTDOMAINS : VsanOverviewDetailsView.SERVICES,
         content: this.clusterRef,
      };
      this.onShowDetails.emit(context);
   }
}
