import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanCapacityService} from "@component/capacity/capacity.service";
import {CapacityData} from "@generated/capacity-data";
import {DatastoreType} from "@generated/datastore-type";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {NavigationService} from "@navigation/navigation.service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {VsanUiUtils} from "@util/vsan-util";
import {DetailsViewContext} from "./details-view-context";
import {VsanOverviewDetailsView} from "./vsan-overview-details-view";

@Component({
   selector: "capacity-card",
   templateUrl: "capacity-card.html",
   styleUrls: ["capacity-card.scss"],
})
export class CapacityCardComponent {
   @Input()
   clusterRef: ManagedObject;

   @Output()
   onShowDetails: EventEmitter<DetailsViewContext> = new EventEmitter<DetailsViewContext>();

   datastoresCapacityData: Map<DatastoreType, CapacityData>;

   capacityData: CapacityData;
   clusterCapabilityData: VsanCapabilityData;

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   error: string;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(private capacityService: VsanCapacityService,
         private capabilitiesService: VsanCapabilityProvider,
         private navigationService: NavigationService) {
   }

   async ngOnInit(): Promise<void> {
      await this.refresh();
      this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
            VsanUiUtils.getString("vsan.monitor.capacity.overview.title"));
   }

   @RefreshHandler
   @Loader()
   async refresh(): Promise<void> {
      [this.datastoresCapacityData, this.clusterCapabilityData] = await Promise.all([
         this.capacityService.getCapacityDataPerDatastore(this.clusterRef, false),
         this.capabilitiesService.getClusterCapabilityData(this.clusterRef),
      ]);
      this.capacityData = this.datastoresCapacityData.get(DatastoreType.VSAN);
   }

   showDetail(): void {
      const context: DetailsViewContext = {
         view: VsanOverviewDetailsView.CAPACITY,
      };
      this.onShowDetails.emit(context);
   }
}
