import {VsanHealthStatus} from '@generated/vsan-health-status';

export interface VsanHealthCheck {

   perspective: string;
   healthGroup: string;
   healthTestId: string;
   healthTest: string;
   healthCheckLabel: string;
   status: VsanHealthStatus;
   isHardwareCheck: boolean;
} //class VsanHealthCheck

export module VsanHealthCheck{
export class Factory {

   static create = (perspective: string, healthGroup: string, healthTestId: string, healthTest: string, healthCheckLabel: string, status: VsanHealthStatus, isHardwareCheck: boolean): VsanHealthCheck => {
      return { perspective: perspective, healthGroup: healthGroup, healthTestId: healthTestId, healthTest: healthTest, healthCheckLabel: healthCheckLabel, status: status, isHardwareCheck: isHardwareCheck, };
   }


} //class Factory

} //module VsanHealthCheck
