import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {HistoricalHealthConfig} from "@generated/historical-health-config";
import {ServiceUtils} from "../services/service.utils";

@Component({
   selector: "historical-health-configuration",
   templateUrl: "historical-health.html",
   styleUrls: ["historical-health.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HistoricalHealthComponent {
   @Input()
   config: HistoricalHealthConfig;

   getStatusLabel(): string {
      return ServiceUtils.enabledStateLabel(this.config.isEnabled);
   }
}
